import React from "react";
import Card from "./Card";

const cardsCollection = (props) => {
  return (
    <>
      <h3 className="text-gray-900 text-xl ">{props.title}</h3>
      <p className="text-gray-600">{props.description}</p>
      <div className="sm:flex sm:-mx-2 sm:overflow-x-auto sm:pb-8 disable-scrollbars sm:-mr-4">
        {props.items.map((item, index) => {
          return (
            <Card
              key={item.title.toLowerCase().replace(/ /g, "_") + index}
              imageUrl={item.imageUrl}
              beds={item.beds}
              baths={item.baths}
              title={item.title}
              reviewCount={item.reviewCount}
              price={item.price}
              rating={item.rating}
            />
          );
        })}
      </div>
    </>
  );
};

export default cardsCollection;
